<template>
    <div>
    <base-dialog :show="!!error" title="An error occured!" @close="handleError">
            <p> {{ error }}</p>
        </base-dialog>
    <section>
        <base-card>
            <header>
                <h2>Requests Received</h2>
            </header>
            <base-spinner v-if="isLoading"></base-spinner>
            <ul v-else-if="hasRequests && !isLoading">
                <request-item 
                v-for="req in receivedRequests" 
                :key=req.id  
                :email="req.userEmail" 
                :message="req.message">
                </request-item>
            </ul>
        <h3 v-else>You haven't received any requests yet!</h3>
        </base-card>
    </section>
</div>
</template>

<script>
import RequestItem from '../../components/requests/RequestItem.vue'
export default{
    components:{
        RequestItem
    },
    data(){
        return{
            isLoading: false,
            error: null
        }
    },
    computed: {
        receivedRequests(){
          return this.$store.getters['requests/requests'];
        },
        hasRequests(){

        const hasRequests = this.$store.getters['requests/hasRequests'];
        console.log('Has Requests:', hasRequests);  // Check if it's true when there are requests
        return hasRequests;        }
    },
    created(){
        this.loadRequests();
    },
    methods:{
        async loadRequests(){
            this.isLoading = true;
            try{
            await this.$store.dispatch('requests/fetchRequests');
            } catch (error){
                this.error = error.message || 'something failed';
            }
                this.isLoading = false;
        },
    
    handleError(){
        this.error = null;
    }
}}
</script>


<style scoped>
    header {
  text-align: center;
}

ul {
  list-style: none;
  margin: 2rem auto;
  padding: 0;
  max-width: 30rem;
}

h3 {
  text-align: center;
}
</style>

